.carouseltitle {
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgb(247, 26, 26);
  font-size: 40px;
  width: 100%;
  height: 80px;
  background-color: white;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande", "Lucida Sans", Arial, sans-serif;
}
.ci {
  height: 400px;
  width: 350px;
  margin-right: 10px;
  border-radius: 23px;
}
.carouselbody {
  background-color: white;
  padding: 20px;
  margin-top: 1px !important;
  margin-bottom: 5px !important;
}
.maincinner {
  background-color: white;
  padding-bottom: 50px;
}
.maincfull {
  margin-bottom: 5px;
}
@media only screen and (max-width: 360px) {
  .ci {
    height: 150px;
    width: 110px;
    margin-right: 5px;
    border-radius: 25px;
    margin-top: 10px;
  }
  .maincinner {
    padding-bottom: 10px;
    padding-left: 7px;
  }
}

@media only screen and (min-width: 361px) and (max-width: 768px) {
  .ci {
    height: 158px;
    width: 115px;
    margin-right: 5px;
    border-radius: 25px;
    margin-top: 10px;
  }
  .maincinner {
    padding-bottom: 10px;
  }
}
